import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useLocation, useNavigate} from 'react-router-dom';
import "./Navigation.css";
import logoImage from '../../img/nav/logo.png';
import home from '../../img/nav/home.png';
import home_click from '../../img/nav/home_click.png';
import recommend from '../../img/nav/recommend.png';
import recommend_click from '../../img/nav/recommend_click.png';
import fireworks from '../../img/nav/fireworks.png';
import fireworks_click from '../../img/nav/fireworks_click.png';
import communication from '../../img/nav/communication.png';
import communication_click from '../../img/nav/communication_click.png';
import myprofile from '../../img/nav/myprofile.png'
import { useAuth } from '../../routers/Login/AuthProvider'
import { toast } from 'react-toastify';
// react-toastify  css
import 'react-toastify/dist/ReactToastify.css';

import { useMediaQuery } from 'react-responsive'

const Mobile = ({ children }) => {
    const isMobile = useMediaQuery({ maxWidth: 768 })
    return isMobile ? children : null
}
const Default = ({ children }) => {
    const isNotMobile = useMediaQuery({ minWidth: 769 })
    return isNotMobile ? children : null
}

function Navigation() {
    const location = useLocation();
    const navigate = useNavigate();
    const { token, setToken, setIsLoggedIn } = useAuth();

    console.log('Navigation :', token);
    const isActive = path => location.pathname === path;

    //     
    const [isTabOpen, setIsTabOpen] = useState(false);


    const handleTabToggle = () => setIsTabOpen(!isTabOpen); //    


    useEffect(() => {
        // token    useEffect .
        console.log('Navigation    :', token);
    }, [token]);

    const handleLogout = () => {
        setToken(null); //    
        navigate('/login');
        toast.success(' .');
    }

    return (
        <div className="nav">
            <Default>
                <>
                <Link to="/" className="home-link">
                    <img src={logoImage} alt="Home Logo" className="home-logo"/></Link> <br></br>
                <Link to="/recommend" className={`recommend-link ${isActive("/recommend") ? "active" : ""}`}>  </Link> <br></br>
                <Link to="/festival" className={`festival-link ${isActive("/festival") ? "active" : ""}`}>/ </Link> <br></br>
                <Link to="/community" className={`community-link ${isActive("/community") ? "active" : ""}`}>  </Link> <br></br>
             {/*   <Link to="/login" className="login-btn">Login</Link>*/}
                    {token ? (
                            <div className="mypage_setting">
                            <div className="mypage-btn" onClick={handleTabToggle}>
                                <img src={myprofile} alt="My Profile" className="mypage" />
                            </div>
                            {isTabOpen && (
                                <div className="bottom-tab">
                                    <Link to="/mypage" className="mypage-btn" onClick={() => setIsTabOpen(false)}></Link>
                                    <div className="logout_text"onClick={() => {
                                        handleLogout();
                                        setIsTabOpen(false);
                                    }}></div>
                                </div>
                                )}
                            </div>
                    ) : (
                        <Link to="/login" className="login-btn">
                            Login</Link>
                    )
                    }
                </>
            </Default>

            <Mobile>
                <>
                    <Link to="/" className="home-link_mobile">
                        <img src={isActive("/")? home_click : home} alt="Home Logo" className="home-mobile-nonclick"/><br/></Link> <br></br>
                    <Link to="/recommend" className="recommend-mobile"> <img src={isActive("/recommend")? recommend_click : recommend} alt="recommend Logo" className="recommend"/><br/>   </Link> <br></br>
                    <Link to="/festival" className="festival-mobile"><img src={isActive("/festival")? fireworks_click : fireworks} alt="fireworks Logo" className="fireworks"/><br/>/ </Link> <br></br>
                    <Link to="/community" className="community-mobile"> <img src={isActive("/community")? communication_click : communication} alt="communication Logo" className="communication"/><br/>  </Link> <br></br>
                    {token ? (
                        <>
                            <div className="mypage_setting">
                            <div className="mypage-btn" onClick={handleTabToggle}>
                                <br/><br/><img src={myprofile} alt="My Profile" className="mypage" /> <br/><p className="mypage_text"></p><br></br>
                            </div>
                            {isTabOpen && (
                                <div className="bottom-tab">
                                    <Link to="/mypage" className="mypage-btn" onClick={() => setIsTabOpen(false)}></Link>
                                    <div className="logout_text" onClick={() => {
                                        handleLogout();
                                        setIsTabOpen(false);
                                    }}></div>
                                </div>
                            )}
                            </div>
                        </>
                    ) : (
                        <Link to="/login" className="login-btn">Login</Link>
                    )
                    }
                </>
            </Mobile>
        </div>
    );
}
export default Navigation;